import React, { useEffect, useState, useRef } from 'react';
import figlet from 'figlet';
import standard from 'figlet/importable-fonts/Standard.js'
import './Home.css';


function Home() {

  const textInput = useRef();
  const linkedin = "https://www.linkedin.com/in/min-thit-tun-421345109/";
  const github = "https://github.com/minthittun";
  const [command, setCommand] = useState("");
  const [name, setName] = useState("");
  const [result, setResult] = useState([]);

  figlet.parseFont('Standard', standard);

  useEffect(() => {

    figlet.text('Min Thit Tun', {
      font: 'Standard',
    }, function (err, data) {
      setName(data);
    });

    setResult([]);

    document.body.addEventListener('click', handleClick);


  }, [])

  function handleClick() {
    textInput.current.select();
  }

  function onKeyDownHandler(e) {
    if (e.key === 'Enter') {
      let cmd = e.target.value.toLowerCase();
      if (cmd === "hi") {

        let msg = "Hi! welcome to my terminal. My name is Min Thit Tun. <br/>I am a full-stack developer. I have experienced in web and mobile <br />application development and designing architecture stacks."
        let msgObj = {
          message: msg,
          time: getLocalTime()
        }
        setResult([...result, msgObj]);

      } else if (cmd === "linkedin") {

        let msg = "Here is my LinkedIn profile. Click <a target='_blank' rel='noreferrer' href = '" + linkedin + "'>here</a> to see."
        let msgObj = {
          message: msg,
          time: getLocalTime()
        }
        setResult([...result, msgObj]);

      } else if (cmd === "github") {

        let msg = "Here is my GitHub. Click <a target='_blank' rel='noreferrer' href = '" + github + "'>here</a> to see."
        let msgObj = {
          message: msg,
          time: getLocalTime()
        }
        setResult([...result, msgObj]);

      } else if (cmd === "help") {

        let msg = `<table>
        <tbody>
        <tr>
        <td>[hi]</td>
        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
        <td >show welcome message.</td>
        </tr>
        <tr>
        <td>[linkedin]</td>
        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
        <td >show LinkedIn URL.</td>
        </tr>
        <tr>
        <td>[github]</td>
        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
        <td >show Github URL.</td>
        </tr>
        <tr>
        <td>[clear]</td>
        <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
        <td >clear all.</td>
        </tr>
        </tbody>
        </table>`

        let msgObj = {
          message: msg,
          time: getLocalTime()
        }
        setResult([...result, msgObj]);

      } else if (cmd === 'clear') {

        setResult([]);

      } else {

        let msgObj = {
          message: "Oops! command not found. Type 'help' to see available commands.",
          time: getLocalTime()
        }
        setResult([...result, msgObj]);

      }

      setCommand("");
      //window.scrollTo(0, document.querySelector(".commandList").scrollHeight);
    }

  }

  function getLocalTime() {
    var today = new Date();

    return today.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', second: 'numeric', hour12: true })

  }

  return (
    <>
      <div className='terminalBody'>
        <div className='commandList'>
          <div>"Hi! welcome to my terminal. My name is Min Thit Tun. <br />I am a full-stack developer. I have experienced in web and mobile <br />application development and designing architecture stacks."</div>
          <div className='marginSpace'></div>
          <pre>{name}</pre>
          <div className='marginSpace'></div>
          <div className='marginSpace'></div>
          <div>Type 'hi' to see the welcome message.</div>
          <div>Type 'help' to see the list of commands.</div>
          <div>Type 'github' or click <a target="_blank" rel="noreferrer" href={github}>here</a> to see my Github.</div>
          <div className='marginSpace'></div>
          <div>
            {
              result.map((data, i) => (
                <div key={i} className='commandItem'>
                  <div style={{ marginBottom: 2 }} dangerouslySetInnerHTML={{ __html: data.message }} />
                  <span style={{ color: '#89aab7', fontSize: 12 }}>~ {data.time}</span>
                </div>
              ))
            }
            <div className='marginSpace2'></div>
          </div>
        </div>
      </div>

      <div className='terminalBottom'>
        <table className='tableWidth' >
          <tbody>
            <tr>
              <td className='tdWidth'>
                <span style={{ color: '#657289' }}>portfolio</span>
                <span style={{ color: '#93a9b6' }}>@</span>
                <span style={{ color: '#96a683' }}>minthittun</span>
                <span style={{ color: '#89aab7' }}>:$ ~ </span>
              </td>
              <td style={{width: 10, paddingTop: 10}}>
                <div className='blinkWrapper'>
                  <div className='text'></div>
                </div>
              </td>
              <td>
                <input ref={textInput} autoFocus className='commandText' type="text" value={command} onChange={e => setCommand(e.target.value)} onKeyDown={onKeyDownHandler} />
              </td>
            </tr>
          </tbody>
        </table>
      </div>

    </>
  );
}

export default Home;
